<template>
    <div class="main">
        <!-- <div class="carlist"> -->
            <v-car-list></v-car-list>
        <!-- </div> -->
    <!-- div -->
        <div class="blank"></div>
        <div class="bg">
            <!--  -->
            <div class="Allprice">
                <p class="price">&nbsp;{{totalPrice}}</p>
                <p >&nbsp;&nbsp;{{seller.deliveryPrice}}</p>
            </div>
            <!--  -->
            <div class="sendprice">
                <p >{{payDesc}}</p>
            </div>
        </div>
        <!--  -->
        <div class="car" @click="carlist(selectFoods)">
            <div class="car2" >
                <i class="icon iconfont icon-gouwuche1"></i>
            </div>
            <div class="count" v-show="totalCount>0">{{totalCount}}</div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import CarList from '../car/carlist.vue';

    export default {
        components:{
            'v-car-list':CarList
        },
        methods:{
            carlist(list){
                console.log(list);
                this.$store.dispatch('carlist',list);
            }
        },
        computed:{
            ...mapGetters([
            'seller',
            'goods',
            'carlistshow'
            ]),
			selectFoods(){
				let foods = [];
				// 
				this.goods.forEach( good => {
					// 
					good.foods.forEach( food => {
						// count0  
						if( food.count > 0 ){
							foods.push(food);
                        }
                        console.log('-------'+food.count);
                    })
                    
				});
				// domdom
				return foods;
			},
			totalPrice(){
				let total = 0;
				// count
				this.selectFoods.forEach( food => {
					total += food.count * food.price;
				});
				return total;
			},
			totalCount(){
				let count = 0 ;
				this.selectFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if( this.totalPrice === 0){
					return `${this.seller.minPrice}`;
				}else if( this.totalPrice < this.seller.minPrice ){
					return `${this.seller.minPrice - this.totalPrice}`
				}else{
					return "";
				}
			}
		}
    }
</script>

<style lang="less" scoped>
.main{
    width: 100%;
    height: 1.16rem;
    position: fixed;
    bottom: 0;
    z-index: 1000;
}
//div div
.blank{
    height: 0.2rem;
    width: 100%;
}
//div
.bg{
    width: 100%;
    height: 0.97rem;
    background: #141d27;
    display: flex;
    justify-content: space-between;
    // 
    .Allprice{
        height: 0.48rem;
        padding-left:1.36rem;
        margin-top: 0.24rem;
        box-sizing: border-box;
        // border: 1px seashell solid;
        p{
            font-size: 0.24rem;
            color: rgba(255,255,255,0.4);
            line-height: 0.48rem;
            display: inline;
            padding: 0 0.24rem;
        }
        .price{
            font-size: 0.32rem;
            font-weight: 900;
            border-right:0.01rem solid rgba(255,255,255,0.1);
        }
    }
    // 
    .sendprice{
        width: 2.1rem;
        height: 100%;
        background: rgba(255,255,255,0.4);
        p{
            height: 0.48rem;
            width: 100%;
            text-align: center;
            margin-top: 0.24rem;
            box-sizing: border-box;
            font-size: 0.24rem;
            color: rgba(255,255,255,0.4);
            line-height: 0.48rem;
        }
    }
}
// 
.car{
    width: 1.12rem;
    height: 1.12rem;
    background: #141d27;
    border-radius: 1rem;
    position: relative;
    top:-1.16rem;
    left: 0.24rem;
    box-sizing: border-box;
    padding: 0.12rem 0.12rem;
    .car2{
        width: 0.88rem;
        height: 0.88rem;
        border-radius: 0.88rem;
        background: rgba(255,255,255,0.2);
        // margin: 0.12rem auto;
        box-sizing: border-box;
        text-align: center;
        
        i{
        //    height: 0.48rem;
           font-size: 0.48rem;
           color: rgba(255,255,255,0.8);
           line-height: 0.88rem
        }
    }
    .count{
        width: 0.48rem;
        height: 0.32rem;
        border-radius:0.2rem;
        background: rgb(240,20,20);
        line-height: 0.32rem;
        font-size: 0.18rem;
        color: rgb(255,255,255);
        font-weight: 700;
        text-align: center;
        position: relative;
        top: -1rem;
        left: 0.5rem;
    }
}
</style>




